body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  display: flex;
  flex-direction: column;
}
.inner-container {
  height: 60px;
  flex-grow: 1;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: white;
  font-size: 20pt;
  font-weight: bolder;
  background-color: #66101F;
  border-bottom: 3px solid #855A5C;
  box-shadow:0 0 8px #30292F;
  font-family: 'Cormorant Garamond', serif;
  font-size: 30px;
}
.tile {
  border: 3px solid #855A5C;
  height: 150px;
  width: 150px;
  margin: 12pt;
  float: left;
  box-shadow:0 0 8px #30292F;
}

.tile:hover{
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1)
}

.game-board {
  width: 70%;
  margin: auto;
  margin-top: 50px;
}

img {
  height: 150px;
  width: 150px;
}

body {
  background-image: url(/Memory-Game/static/media/wallpaper.cf895185.jpg);
  background-size: 120%;
}
